<template>
	<div id="app">
		<div class="wrapper clearfix">
			<Players :scorePlayer="scorePlayer"
					:currentScore="currentScore"
					:activePlayer="activePlayer"
					:isWinner="isWinner"
					/>
			<Controls 
				@handleHoldScore ="handleHoldScore"
				@handleNewGame="handleNewGame"
				@handleRollDice="handleRollDice"
				:finalScore="finalScore"
				@handleChangeFinalScore="handleChangeFinalScore"
				:isPlaying="isPlaying"
			/>
			<Dices 
				:dices = "dices"
			/>
			<PopUp 
			:isOpenPopUp="isOpenPopUp"
			@handleSubmit="handleSubmit"
			/>
		</div>
		
		
	</div>
</template>

<script>
	import Players from './components/Players.vue'
	import Controls from './components/Controls'
	import Dices from './components/Dices'
	import PopUp from './components/Popup'
export default {
	name: 'app',
	data () {
		return {
			isPlaying: false,
			scorePlayer: [0,0],
			currentScore: 0,
			activePlayer: 0,
			dices: [0,0],
			isOpenPopUp: false,
			finalScore : 10
			
		}
	},
	components:{
		Players,
		Controls,
		Dices,
		PopUp
	},
	computed:{
		isWinner(){
			let {scorePlayer , finalScore} = this
			if(scorePlayer[0] >= finalScore || scorePlayer[1] >= finalScore){
				this.isPlaying = false
				return true
			}
			return false
		}
	},
	methods:{
		handleChangeFinalScore(e){
			console.log(e.target.value)
		},
		handleHoldScore(){
			let {scorePlayer, currentScore, activePlayer} = this
			let scoreOld = scorePlayer[activePlayer]
			// let cloneScorePlayer = [...scorePlayer]
			// cloneScorePlayer[activePlayer] = scoreOld + currentScore
			this.$set(this.scorePlayer,activePlayer,currentScore + scoreOld)
			// this.scorePlayer = cloneScorePlayer
			if(!this.isWinner){
				this.nextPlayer()
			}	
		},
		nextPlayer(){

			this.activePlayer = this.activePlayer ==  0 ? 1 : 0
			this.currentScore = 0
		},
		handleNewGame(){
			console.log("handleNewGame Appvue");
			this.isOpenPopUp = true
		},
		handleSubmit(){
			console.log("handleSubmit Appvue")
			this.isPlaying = true,
			this.isOpenPopUp = false,
			this.currentScore = 0,
			this.scorePlayer[0] = 0,
			this.scorePlayer[1] = 0,
			this.dices = [0,0]
			console.log("da set ve 0")
		},
		handleRollDice(){
			if(this.isPlaying === true){
					var dice1 = Math.floor(Math.random()*6 +1)
					var dice2 = Math.floor(Math.random()*6 +1)
					console.log(dice1,dice2)
					this.dices = [dice1,dice2]
					this.currentScore += this.dices[0] + this.dices[1]
				
				if( this.dices[0] == 1 || this.dices[1] ==1){
					setTimeout(()=>{
							alert("Ban da quay trung so 1")
					},150)
					
					this.nextPlayer()
					
				}
				else{
					
				}
			}
			else
				alert("vui long nhan new game")
			
		}
	}
}
</script>

<style>
	* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
}

.clearfix::after {
    content: "";
    display: table;
    clear: both;
}

body {
    background-image: linear-gradient(rgba(62, 20, 20, 0.4), rgba(62, 20, 20, 0.4)), url('/public/assets/back.jpg');
    background-size: cover;
    background-position: center;
    font-family: Lato;
    font-weight: 300;
    position: relative;
    height: 100vh;
    color: #555;
}

.wrapper {
    width: 1000px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.3);
    overflow: hidden;
}



/**********************************************
*** Control
**********************************************/


/* DICE */


</style>



