<template>
  <div class="modal-popup" :class="{'open-popup': isOpenPopUp}">
    <div class="rule">
      <h1>Rule</h1>
      <p>
        Lorem ipsum dolor sit amet consectetur adipisicing elit.
        Consequatur laborum molestias quisquam! Fugit modi ad rem quibusdam,
        consectetur in optio nulla sit blanditiis alias maxime ut laboriosam o
        dio deleniti quod?
      </p>
      <button @click="submit" class="rule-submit">OK</button>
    </div>
  </div>
</template>
<script>
// JS
export default {
  name: "PopUp",
  props: ['isOpenPopUp'],
  data() {
    return {
        
    };
  },
  methods:{
      submit(){
          console.log('Submit PopUp')
          this.$emit('handleSubmit')          
      }
  }
};
</script>
<style >
/* CSS */
.modal-popup{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.4);
    opacity: 0;
    visibility: hidden;
    transition: .2s all ease;
}
.modal-popup.open-popup{
    opacity: 1 !important;
    visibility: visible !important;
}
.rule {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1.2);
  transition: .2s all ease;
  max-width: 300px;
  background: white;
  padding: 10px 20px;
}
.rule p{
    margin: 10px 0;
}
button.rule-submit{
    padding: 10px 20px;
    border-radius: 999px;
    outline: none;
    border: none;
}
button.rule-submit:hover{
    background-color: rgba(0, 0, 0, 0.4);
    color: white
}
.open-popup .rule{
    transform: translate(-50%, -50%) scale(1);
    transition: .2s all ease;
}
</style>


